html {
    font-size: 6.25%; /* reset 1rem to 1px */
}

body {
    font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    font-size: 18rem; /* 18px */
    line-height: 1.2;
    font-weight: 300;
    color: #555559;
    background: #fff;
    cursor: default;
}

h1 {
    font-size: 3.111em;
    margin: 0 0 1.5em;
}

h2 {
    font-size: 1.714em;
    margin: 0 0 1.5em;
}

h3 {
    font-size: 1.286em;
    margin: 0 0 1.5em;
}

a {
    cursor: pointer;
    text-decoration: none;
    color: #286782;
}

a:hover {
    color: #a6a6a6;
    transition: color 0.12s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

img {
    border: none;
    max-width: 100%;
}

ul,
ol,
article p {
    margin: 0 0 1em;
}

ul, ol {
    padding: 0;
}

ul li {
    padding: 0;
    margin: 0 0 0.25em 1em;
}

ol li {
    padding: 0;
    margin: 0 0 0.25em 1em;
}

hr {
    color: 1px solid #84959c;
}

/* for Chrome, Opera, Android, Safari */

::-webkit-scrollbar {
    width: 20px;
    height: 20px;
    background: none;
}
::-webkit-scrollbar-track {
    background: none;
    display: none;
}
::-webkit-scrollbar-thumb {
    border: 7px solid transparent;
    background: rgba(212, 214, 219, 0.5);
    background-clip: padding-box;
    border-radius: 10px;
}
::-webkit-scrollbar-button
::-webkit-scrollbar-track-piece,
::-webkit-scrollbar-corner,
::-webkit-resizer {
    display: none;
}

/* for firefox */

* {
    scrollbar-color: rgba(212, 214, 219, 0.5) transparent;
    scrollbar-width: thin;
}

html {
    position: relative;
    min-height: 100%;
}

body {
    margin: 0 0 80px; /* margin-bottom = footer height */
    background-color: #fbfafa;
}

@media (max-device-width: 920px) {
    body {
        margin-bottom: 140px;
    }
}

footer {
    position: absolute;
    left: 0;
    bottom: 0;
    height: 100px;
    width: 100%;
}

@media (max-device-width: 600px) {
    body {
        margin-bottom: 180px;
    }
}

.page-header {
    display: block;
    width: 100%;
    height: auto;
    font-size: 1em;
    background-color: #fff;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    overflow: auto;
}

.mainmenu .menu-list {
    text-align: center;
    margin: 0;
    position: relative;
}

.mainmenu .menu-item {
    display: inline-block;
    vertical-align: baseline;
    margin: 0;
    padding: 0 40px;
    font-size: 1.111em;
    line-height: 5em;
    text-transform: uppercase;
    font-weight: 600;
}

.mainmenu .menu-item a {
    color: #2e2e2e;
}

.mainmenu .menu-item a:hover {
    color: #a6a6a6;
    transition: color 0.12s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}


#main-nav-toggle {
    float: right;
    display: none;
    color: #000;
    font-weight: 600;
    font-size: 16rem;
    line-height: 2.9em;
    overflow: hidden;
    margin: 0;
    padding: 2px 2px 0;
    background: transparent;
    border: 0;
}

.mainmenu .dropdown-icon {
    content: "";
    display: inline-block;
    width: 5px;
    height: 5px;
    margin-bottom: 3px;
    margin-left: 8px;
    vertical-align: baseline;
    border-top: 1.5px solid #111;
    border-right: 1.5px solid #111;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}

@media (max-device-width: 766px) {
    .mainmenu .menu-item {
        padding: 0 20px;
        line-height: 3em;
        text-transform: none;
        font-size: 1em;
    }
}

@media (max-device-width: 600px) {
    .mainmenu {
        height: 48px;
        position: relative;
    }

    #main-nav-toggle {
        position: relative;
        display: block;
        float: right;
        position: absolute;
        right: 32px;
        top: 0;
    }

    .mainmenu.responsive .menu-list {
        overflow: auto;
        padding: 40px 0;
    }

    .mainmenu .menu-item {
        display: none;
        text-transform: none;
    }

    .mainmenu .menu-item a {
        display: block;
    }

    .mainmenu.responsive {
        height: auto;
        height: initial;
    }

    .mainmenu.responsive .menu-item {
        display: block;
        text-align: left;
        padding: 0;
        margin: 0 28px;
        font-weight: 500;
    }

    .mainmenu.responsive .menu-item a::after {
        content: "";
        display: block;
        float: right;
        margin-top: 22px;
        width: 5px;
        height: 5px;
        margin-bottom: 3px;
        margin-left: 3px;
        border-top: 1.2px solid rgba(255, 255, 255, 0.2);
        border-right: 1.2px solid rgba(255, 255, 255, 0.2);
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
        transition: -webkit-transform .3s;
        transition: transform .3s;
        transition: transform .3s, -webkit-transform .3s;
    }

    .mainmenu.responsive .dropdown-icon {
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
        margin-bottom: -1.5px;
        transition: all 0.35s cubic-bezier(0.165, 0.84, 0.44, 1);
    }


    .mainmenu.responsive .menu-item + .menu-item {
        border-top: 1px solid rgba(0, 0, 0, 0.1);
    }
}

.images-grid {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    background-color: #fff;
}

.image-wrapper {
    width: 100%;
    min-width: 200px;
    height: 200px;
    position: relative;
    overflow: hidden;
    margin: 0;
}

.images-grid .image-wrapper img {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    min-height: 100%;
    min-width: 100%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

@media (max-width: 1200px) {
    .images-grid {
        grid-template-columns: repeat(4, 1fr);
        max-height: 400px;
        overflow-y: hidden;
    }
}

@media (max-width: 800px) {
    .images-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 800px) {
    .image-wrapper {
        min-width: 0;
        min-width: initial;
        min-height: 0;
        min-height: initial;
    }
}

.page-footer {
    height: 5.714em;
    display: block;
    font-size: 0.778em;
    font-weight: 400;
    color: #555559;
    background-color: #efefef;
    box-sizing: border-box;
}

.page-footer .content-wrapper {
    text-align: right;
    margin: 0;
    padding: 2.286em 20px;
    width: 100%;
    box-sizing: border-box;
}

.footer-info {
    margin: 0;
    display: inline-block;
}

.footer-info > .footer-item {
    padding: 0;
    margin: 0;
    display: inline-block;
    vertical-align: baseline;
}

.footer-info > .footer-item + .footer-item::before {
    content: '|';
    margin: 0 20px;
}

.page-footer .copyright {
    float: left;
}

@media (max-device-width: 920px) {
    .page-footer {
        height: 140px;
    }

    .page-footer .content-wrapper {
        height: 100%;
    }

    .footer-info > .footer-item {
        display: block;
        line-height: 1.8em;
    }

    .footer-info > .footer-item + .footer-item::before {
        content: none;
    }
}

@media (max-device-width: 600px) {
    .page-footer {
        height: 180px;
    }

    .footer-info {
        display: block;
        float: left;
        text-align: left;
        margin-bottom: 20px;
    }

    .footer-info > .footer-item + .footer-item::before {
        border-top: 1px solid #000;
    }
}

.primary-button {
    font-size: 20px;
    height: 3.2em; /* 64px */
    line-height: 1.2em; /* 24px */
    padding: 1em 18px;
    display: inline-block;
    color: #fff;
    font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    font-weight: 600;
    border-radius: 5px;
    background: #286782;
    border: none;
    box-shadow: none;
    cursor: default;
    margin: 0;
}

.primary-button::-moz-focus-inner {
    border: 0;
    padding: 0;
}

@media (max-device-width: 600px) {
    .primary-button {
        width: 100%;
    }
}

.hotdogs-list .hotdog-item {
    display: block;
    width: 100%;
    background-color: #ffffff;
    overflow-x: auto;
    overflow-y: hidden;
}

.hotdogs-list .header-wrapper {
    float: left;
    width: 50%;
    padding-top: 14%;
    padding-left: 10%;
    box-sizing: border-box;
}

.hotdog-item h2 {
    display: block;
    font-size: 1.333em;
    font-weight: 700;
    color: #555559;
    letter-spacing: -1px;
    position: relative;
    max-width: 300px;
}

.hotdog-item h2::before {
    --line-width: 1.333em;
    width: var(--line-width);
    top: 0.5em;
    height: 2px;
    content: '';
    position: absolute;
    left: calc((20px + var(--line-width)) * -1);
    background-color: #286782;
}

.hotdog-item .hotdog-description {
    display: block;
    font-size: 1em;
    line-height: 1.333em;
    font-weight: 300;
    max-width: 360px;
}

.hotdogs-list .image-wrapper {
    display: block;
    float: right;
    height: 0;
    width: 50%;
    padding-top: 40%; /* 2:5 Aspect Ratio */
    position: relative;
}

.hotdogs-list .article-image {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.hotdog-item:nth-child(2n) .image-wrapper {
    float: left;
}

.hotdog-item:nth-child(2n) .header-wrapper {
    float: right;
}

.articles-failure-warning, .articles-preloader {
    font-size: 1.5em;
    font-weight: 500;
    padding-bottom: 40px;
    display: block;
    text-align: center;
}

@media (max-device-width: 992px) {
    .hotdogs-list {
        padding: 0 32px 80px;
        box-sizing: border-box;
    }

    .hotdogs-list .hotdog-item {
        padding: 100px 36px 0;
        box-sizing: border-box;
        width: 100%;
        max-width: 600px;
        margin: 0 auto 40px;
        overflow: auto;
        box-shadow: 0 0 1px rgba(0, 0, 0, 0.2), 0 20px 40px rgba(0, 0, 0, 0.1);
        border-radius: 6px;
    }

    .hotdogs-list .image-wrapper {
        float: none;
        width: 100%;
        padding-top: 80%; /* 4:5 Aspect Ratio */
        margin: 0 auto;
        border-radius: 4px 4px 0 0;
    }

    .hotdogs-list .header-wrapper {
        float: none;
        width: 100%;
        padding: 0;
    }

    .hotdog-item:nth-child(2n) .image-wrapper {
        float: none;
    }

    .hotdog-item:nth-child(2n) .header-wrapper {
        float: none;
    }

    .hotdog-item h2 {
        font-size: 1.778em;
        max-width: 100%;
    }

    .hotdog-item h2::before {
        position: absolute;
        left: 0;
        top: -40px;
    }

    .hotdog-item .hotdog-description {
        max-width: 100%;
        margin-bottom: 32px;
        font-weight: 400;
        line-height: 1.5em;
    }
}

@media (max-device-width: 600px) {
    .hotdogs-list {
        padding: 0 20px 80px;
    }

    .hotdogs-list .hotdog-item {
        padding: 100px 0 0;
    }

    .hotdog-item .header-wrapper {
        padding: 0 24px;
    }

    .hotdogs-list .image-wrapper {
        border-radius: 0;
    }
}

