input {
    display: inline-block;
    width: auto;
    height: auto;
    line-height: 1.3em;
    font-size: 16px;
    font-family: inherit;
    border-radius: 5px;
    color: #000;
    padding: calc(1.125em / 2) 13px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-sizing: border-box;
    box-shadow: none;
    background: inherit;
    background-clip: padding-box;
}

input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

input:focus {
    outline: 1px dashed #888;
}

input:focus:hover {
    outline: transparent;
}

textarea {
    display: inline-block;
    width: auto;
    height: auto;
    line-height: 1.125em; /* 18px */
    font-size: 16px;
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    border-radius: 5px;
    color: #000;
    padding: calc(1.5em / 2) 13px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-sizing: border-box;
    box-shadow: none;
    background: inherit;
    resize: vertical;
    min-height: 5em; /* rows * line-height + padding-top + paddint-bottom + borders */
    overflow: auto;
    background-clip: padding-box;
}

textarea:focus {
    outline: 1px dashed #888;
}

textarea:focus:hover {
    outline: transparent;
}

label {
    display: inline-block;
    width: 310px;
    margin-bottom: 22px;
    color: #000000;
    font-size: 12px;
    font-weight: 500;
    text-align: left;
    line-height: normal;
    vertical-align: bottom;
}

label.required::before {
    content: "* ";
}

label input,
label textarea,
label select {
    display: block;
    width: 100%;
    margin-top: 2px;
    margin-right: 12px;
}

label input[type="checkbox"],
label input[type="radio"] {
    display: inline-block;
}

button {
    font-size: 20px;
    height: 3.2em; /* 64px */
    line-height: 1.2em; /* 24px */
    padding: 1em 18px;
    display: inline-block;
    color: #fff;
    font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    font-weight: 600;
    border-radius: 5px;
    background: #286782;
    border: none;
    box-shadow: none;
    cursor: default;
    margin: 0;
}

button::-moz-focus-inner {
    border: 0;
    padding: 0;
}

button:disabled {
    opacity: 0.7;
}

@media (max-device-width: 600px) {
    .contact-form .submit-button {
        width: 100%;
    }
}

.contact-page {
    padding: 120px 0 180px;
    text-align: center;
}

.contact-form {
    display: inline-block;
    margin: 0 auto;
    color: #000;
}

.contact-form h1 {
    margin-bottom: .5em;
    font-size: 2.5em;
    color: inherit;
    font-weight: 500;
}

.contact-form small {
    max-width: 500px;
    display: inline-block;
    font-size: 16px;
    color: inherit;
    margin-bottom: 4em;
    line-height: 2em;
}

.contact-form .input-field {
    display: block;
    margin-left: auto;
    margin-right: auto;
    color: inherit;
}

.contact-form .submit-button {
    font-size: 1em;
}

.contact-form .control-error {
    border: 1px solid red;
}

@media (max-device-width: 600px) {
    .contact-form {
        padding-left: 20px;
        padding-right: 20px;
    }

    .contact-form .input-field {
        width: 100%;
    }
}

